@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;1,700&display=swap);
  
body{
    height: 100vh;
    background-image: linear-gradient(45deg, rgb(245, 182, 8) 55%, rgb(51, 247, 247));
    font-family: "Roboto", sans-serif;
}

div{
    text-align: center;
}

h1{
    color: whitesmoke;
}

button{
    font-size: 1.2em;
    background-color: aqua;
    margin: 20px;
    padding: 10px;
    border-radius: 15px;
    border: 0;
    cursor: pointer;
}

button:hover{
    opacity: 0.8;
    -webkit-transform: scale(0.98);
            transform: scale(0.98);
}

img{
    max-width: 500px;
}


